/*** The new CSS reset - version 1.5.1 (last updated 1.3.2022) ***/
*:where(:not(iframe, canvas, img, svg, video):not(svg *, symbol *)) {
  all: unset;
  display: revert;
}

*, *::before, *::after {
  box-sizing: border-box;
}

a, button {
  cursor: revert;
}

ol, ul, menu {
  list-style: none;
}

img {
  max-width: 100%;
}

table {
  border-collapse: collapse;
}

textarea {
  white-space: revert;
}

meter {
  -webkit-appearance: revert;
  appearance: revert;
}

::placeholder {
  color: unset;
}

:where([hidden]) {
  display: none;
}

:where([contenteditable]) {
  -moz-user-modify: read-write;
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  -webkit-line-break: after-white-space;
}

:where([draggable=true]) {
  -webkit-user-drag: element;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
  font-family: "Noto Sans JP", sans-serif, "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  color: #000;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  transition: 0.3s;
}
a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
  vertical-align: middle;
  object-fit: cover;
  width: 100%;
}

input {
  margin: 0;
  padding: 0;
}

textarea {
  padding: 0;
  font: inherit;
  color: inherit;
}

button {
  font: inherit;
  padding: 0;
  background-color: transparent;
  border: none;
  color: inherit;
  cursor: pointer;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.4rem;
}

@media screen and (min-width: 768px) {
  .sp {
    display: none;
  }
}
@media screen and (max-width: 599px) {
  .pc {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  .l-header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    transition: 0.5s;
  }
  .l-header__wrap {
    margin: 0 auto;
    display: grid;
    grid-template-columns: 200px 1fr;
    align-items: center;
    gap: 30px;
    width: 100%;
  }
  .l-header__logo {
    width: 200px;
  }
  .l-header.is-scroll {
    background: #fff;
  }
}
@media screen and (max-width: 599px) {
  .l-header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    transition: 0.5s;
  }
  .l-header__wrap {
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 30px;
    width: 100%;
  }
  .l-header__logo {
    width: 100%;
  }
  .l-header.is-scroll {
    background: #fff;
  }
}
@media screen and (max-width: 599px) {
  .l-hamburger {
    z-index: 1;
    position: absolute;
    width: 30px;
    height: 30px;
    top: 5px;
    right: 5%;
    transition-duration: 0.5s;
  }
  .l-hamburger__icon {
    transition-duration: 0.5s;
    position: absolute;
    height: 2px;
    width: 30px;
    top: 20px;
    background-color: #000;
  }
  .l-hamburger__icon:before {
    transition-duration: 0.5s;
    position: absolute;
    width: 30px;
    height: 2px;
    background-color: #000;
    content: "";
    top: -10px;
  }
  .l-hamburger__icon:after {
    transition-duration: 0.5s;
    position: absolute;
    width: 30px;
    height: 2px;
    background-color: #000;
    content: "";
    top: 10px;
  }
  .l-hamburger.is-open .l-hamburger__icon {
    transition-duration: 0.5s;
    background: transparent;
  }
  .l-hamburger.is-open .l-hamburger__icon:before {
    transform: rotateZ(45deg) scaleX(1.25) translate(7px, 7px);
  }
  .l-hamburger.is-open .l-hamburger__icon:after {
    transform: rotateZ(-45deg) scaleX(1.25) translate(6px, -6px);
  }
  .l-hamburger__list {
    display: none;
  }
  .l-hamburger__list.is-open {
    display: block;
    z-index: 1;
    width: 100%;
    height: 100vh;
    background: #ccc;
    position: absolute;
    top: 0;
    left: 0;
  }
  .l-hamburger:hover {
    cursor: pointer;
  }
}
@media screen and (min-width: 768px) {
  .l-nav {
    margin: 0 20px 0 0;
  }
  .l-nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }
  .l-nav__list__item a {
    display: block;
    font-size: 1.6rem;
  }
  .l-nav__list__item a:hover {
    color: #ccc;
  }
  .l-nav__list__item:not(:last-of-type) {
    margin: 0 20px 0 0;
  }
}
@media screen and (max-width: 599px) {
  .l-nav__list {
    position: absolute;
    top: 0;
    right: -60%;
    width: 60%;
    height: 100vh;
    background: #ccc;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    flex-direction: column;
    padding: 20vh 0 0;
    transition: 0.3s;
  }
  .l-nav__list__item {
    width: 100%;
  }
  .l-nav__list__item a {
    display: block;
    text-align: center;
    padding: 5vh 0;
  }
  .l-nav.is-open .l-nav__list {
    right: 0;
  }
}
